* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-appearance: none; }

html {
  font-family: 'Roboto', sans-serif;
  font-size: 62.5%; }
  @media only screen and (max-width: 600px) {
    html {
      font-size: 59%; } }

.container {
  width: calc(100% - calc(2 * 4rem));
  max-width: 120rem;
  margin: 0 auto; }
  @media only screen and (max-width: 600px) {
    .container {
      width: calc(100% - calc(2 * 1.5rem)); } }

.page-grid {
  display: grid;
  grid-template-columns: [flush-start] minmax(4rem, 1fr) [container-start] repeat(12, [col-start] minmax(min-content, 10rem) [col-end]) [container-end] minmax(4rem, 1fr) [flush-end];
  grid-template-rows: 65vh; }
  @media only screen and (max-height: 750px) {
    .page-grid {
      grid-template-rows: 100vh; } }
  @media only screen and (max-width: 600px) {
    .page-grid {
      grid-template-columns: [flush-start] minmax(1.5rem, 1fr) [container-start] repeat(12, [col-start] minmax(min-content, 12.5rem) [col-end]) [container-end] minmax(1.5rem, 1fr) [flush-end]; } }

body {
  font-size: 1.6rem;
  color: #646F79; }

.heading {
  color: #353A47;
  font-size: 3rem;
  margin-bottom: 4rem; }

.heading-secondary {
  color: #2A4660;
  font-size: 2.4rem;
  margin-bottom: 2rem; }

p {
  line-height: 2.4rem; }

span.highlight {
  color: #4497E2;
  font-weight: 500; }

.link {
  color: #4497E2;
  text-decoration: none; }
  .link i {
    margin-right: .5rem; }
  .link--bold {
    font-weight: 500; }
  .link--inactive {
    pointer-events: none;
    color: rgba(68, 151, 226, 0.5); }

.small-section-title {
  color: #2A4660;
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  font-weight: 600; }

.footer {
  grid-column: flush-start / flush-end;
  padding: 7.5rem 0;
  background: #4497E2; }
  .footer .container {
    text-align: center;
    color: white; }
    .footer .container h1 small {
      font-size: 1.4rem;
      font-weight: 400; }
  .footer__actions {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(min-content, 7rem));
    grid-column-gap: 1rem;
    grid-row-gap: 3rem;
    width: 100%;
    justify-content: center;
    margin-top: 8rem; }
    .footer__actions .action {
      color: rgba(255, 255, 255, 0.6); }
      .footer__actions .action:hover {
        color: white; }

.action {
  color: inherit;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  text-align: center;
  transition: 150ms ease-in-out;
  transform: translateY(0); }
  .action:hover {
    transform: translateY(-1rem); }
  .action i {
    font-size: 2rem; }
  .action span {
    margin-top: 1rem;
    font-size: 1.8rem;
    font-weight: 300; }

@keyframes fadeSlideInPure {
  0% {
    transform: translateY(4rem);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

@keyframes fadeSlideOutPure {
  0% {
    transform: translateY(0rem);
    opacity: 1; }
  100% {
    transform: translateY(4rem);
    opacity: 0; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

#modal-root {
  position: relative;
  z-index: 2; }

.modal-container {
  padding-top: 6rem;
  background: rgba(0, 0, 0, 0.45);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  animation: fadeIn 150ms ease forwards; }
  .modal-container--animate-out {
    animation: fadeOut 150ms ease forwards; }
  .modal-container .modal {
    background: white;
    border-radius: 5px;
    box-shadow: 0 1rem 1.5rem rgba(0, 0, 0, 0.3);
    width: 40rem;
    max-width: 90%;
    animation: fadeSlideInPure 150ms ease forwards; }
    .modal-container .modal--animate-out {
      animation: fadeSlideOutPure 150ms ease forwards; }
    .modal-container .modal__title, .modal-container .modal__body, .modal-container .modal__actions {
      padding: 1.3rem; }
    .modal-container .modal__title, .modal-container .modal__body {
      border-bottom: 1px solid #e2e2e2; }
    .modal-container .modal__title {
      color: black;
      text-align: center; }
      .modal-container .modal__title p {
        margin: 0; }
    .modal-container .modal__body {
      font-size: 1.4rem;
      font-weight: 400;
      color: black; }
    .modal-container .modal__actions {
      display: grid;
      grid-column-gap: 1.3rem;
      grid-template-columns: repeat(auto-fit, minmax(min-content, 14rem));
      justify-content: end; }

.my-email {
  text-align: center;
  padding: 4rem; }
